  padding: 5px;
  font-size: 12px;
  line-height: 1;
}

.button-cover, .knobs, .layer {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.button {
  width: 36px;
  height: 17px;
  margin: -20px auto 0;
  position: relative;
  top: 50%;
  overflow: hidden;
}

.button.r, .button.r .layer {
  border-radius: 100px;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  z-index: 1;
  background-color: #ebf7fc;
  transition: all .3s;
}

.checkbox {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
  margin: 0;
  padding: 0;
  position: relative;
}

#button-1 .knobs:before {
  content: "ON";
  width: 10px;
  height: 5px;
  color: #fff;
  text-align: center;
  background-color: #200852;
  border-radius: 50%;
  padding: 4px 2px;
  font-size: 5px;
  font-weight: bold;
  line-height: 1;
  transition: all .3s cubic-bezier(.18, .89, .35, 1.15);
  position: absolute;
  top: 2px;
  left: 4px;
}

#button-1 .checkbox:checked + .knobs:before {
  content: "OFF";
  background-color: #8c130a;
  left: 18px;
}

@media screen and (min-width: 1158px) {
  .button {
    width: 50px;
